import React from 'react';
import ThreeScene from './ThreeScene';

function App() {
  let images = [];
  for (let i = 1; i <= 189; i++) {
    images.push(`${process.env.PUBLIC_URL}/images/image${i}.png`);
  }

  let images_murs = [];
  for (let i = 1; i <= 37; i++) {
    images_murs.push(`${process.env.PUBLIC_URL}/images_murs/mur${i}.png`);
  }

  console.log(images_murs);

  const dalleImage = `${process.env.PUBLIC_URL}/image_sol/dalle.png`;

  return (
    <div className="App">
      <ThreeScene images={images} dalleImage={dalleImage} images_murs={images_murs} />
    </div>
  );
}

export default App;

